<template>
    <view class="detail-container">
        <view class="form-group">
            <view class="form-item">
                <text class="label">客户姓名</text>
                <view class="input-box">
                    <text>{{schoolInfo.name}}</text>
                </view>
            </view>
            
            <view class="form-item">
                <text class="label">电话</text>
                <view class="input-box">
                    <text>{{schoolInfo.phone}}</text>
                </view>
            </view>
            
            <view class="form-item">
                <text class="label">客户等级</text>
                <view class="input-box">{{getLevelText(schoolInfo.clientLevel)}}</view>
            </view>

            <view class="form-item">
                <text class="label">客户状态</text>
                <view class="input-box">{{ getStatusText(schoolInfo.clientStatus) }}</view>
            </view>
            
        </view>
    </view>
</template>

<script>
const util = require("@/utils/util.js")
export default {
    data() {
        return {
            id: '', // 存储路由传递的id参数
            schoolInfo: {
                clientStatus: '',
                name: '',
                phone: '',
                clientLevel: ''
            },
        }
    },
    onLoad(options) {
        if (options.id) {
            this.id = options.id;
            this.getSchoolDetail();
        }
    },
    methods: {
        getSchoolDetail() {
            // 这里是模拟数据，实际开发时替换为接口调用
            let that=this;
            uni.showLoading({title: '加载中',mask:true});
            util.request('wx/api/clientDetail',{id:this.id}, 'GET', 'application/json', false, true)
				.then(res => {
					uni.hideLoading()
					if (res.code === 200) {
						that.schoolInfo = res.data;
					} else {
						uni.showToast({
							title: res.msg ,
							icon: 'none'
						})
					}
				})
				.catch(err => {
					uni.hideLoading()
					uni.showToast({
						title: '网络异常，请稍后再试',
						icon: 'none'
					})
					
				})
				
        },
        // 根据状态获取对应的文本
		getStatusText(status) {
			console.log('status',status);
			const statusTexts = {
				0: '待跟进',
				1: '意向中',
				2: '已签约',
				3: '已放弃'
			};
			return statusTexts[status] || '待跟进';
		},

        getLevelText(level) {
			const levelTexts = {
				1: '普通客户',
				2: 'VIP客户',
			};
			return levelTexts[level] || '普通客户';
		},
    }
}
</script>

<style scoped>
.detail-container {
    padding: 20rpx;
    background: #f5f5f5;
    min-height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}

.form-group {
    border-radius: 16rpx;
    padding: 0 30rpx;
    width: 100%;
    max-width: 1200rpx;
    background-color: #fff;
    box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.05);
    margin: 20rpx;
}

.form-item {
    display: flex;
    align-items: center;
    padding: 30rpx 0;
    border-bottom: 1rpx solid #eee;
}

.form-item:last-child {
    border-bottom: none;
}

.label {
    width: 180rpx;
    font-size: 28rpx;
    color: #333;
    margin-left: 10rpx;
    font-weight: 500;
}

.required {
    color: #FF5722;
    margin-left: 4rpx;
}

.input-box {
    flex: 1;
    font-size: 28rpx;
    color: #666;
    padding: 0 20rpx;
}
</style>